* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: rgb(9, 78, 9);
  padding: 2rem;
}

nav {
  display: flex;
  justify-content: space-between;
  padding: 1rem;
  background-color: gray;
}

nav a {
  font-size: 1.5rem;
  text-decoration: none;
  font-weight: 600;
  color: white;
}

nav a:not(nav a:last-child) {
  margin-right: 3rem;
}

.container {
  display: flex;
  justify-content: space-between;
  padding: 1rem 1rem 0;
  margin-top: 1rem;
  background-color: darkgreen;
}

.yard {
  background-color: black;
}

.col {
  display: flex;
  flex-direction: column;
  width: 31%;
  padding: 1rem;
  margin-bottom: 1rem;
  background-color: black;
}

.yard .col {
  background-color: darkslategray;
}


.col:nth-child(2) {
  background-color: gray;
}

.col h2 {
  margin-bottom: 1rem;
  background-color: white;
  padding: 0.5rem 1rem;
}

.yard .col h2 {
  background-color: white;
}

form {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  background-color: white;
}

form:not(form:last-child) {
  margin-bottom: 1rem;
}

form header {
  display: flex;
  justify-content: space-between;
}

form footer {
  margin-top: 0.5rem;
  display: flex;
  justify-content: space-between;
}

.material {
  font-weight: 700;
}

.customer {
  font-weight: 500;
}

.status-icon {
  margin-right: 1rem;
  color: darkgreen;
}


button {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background-color: chocolate;
  background-color: transparent;
  border: none;
}







.search-box {
  margin-bottom: 1rem;
}

.icon {
  font-size: 2rem;
  color: white;
  padding: 3px;
  background-color: red;
}

.search-icon {
  position: relative;
  left: 0.5rem;
}

.search-input {
  background-color: transparent;
  border: 2px solid white;
  padding: 0.5rem 1rem;
  margin-bottom: 1rem;
  font-size: 1rem;
  width: 100%;
  color: white;
}

label {
  font-size: 1.25rem;
  margin-right: 1rem;
  color: white;
}

label input {
  height: 1rem;
  width: 1rem;
}

@media screen and (max-width: 1000px) {
  .container {
    flex-direction: column;
  }

  .col {
    width: 100%;
  }
}